.App {
  display: flex;
  flex-direction: column;
  width: 180vw;
  height: 380vh;
}


nav {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  padding: 1.5rem;
  background-color: #0277bd;
  color: white;
}

.contain {
  display: flex;
  width: 100vw;
  justify-content: space-around;
  flex-direction: row;
  padding-left: 1rem;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
}
.body {
  position: relative;
  flex-grow: 1;
  background: url('./assets/festival.jpg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
}

h2 {
  flex-grow: 1;
}

.find { 
  transition: transform 0.2s ease-in-out;
}

.find:hover {
  transform: scale(2.5);
}



.signIn {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

img {
  padding: 1rem;
  border-radius: 6rem;
  height: 6rem;
}

button {
  cursor: pointer;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-weight: 800;
  font-size: 1rem;
  border: none;
  background-color: transparent;
  color: white;
  border-radius: 2rem;
  padding: 1rem;
}